import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {RwdPriceBox} from "../../../shared/PriceBoxes";
import {LRow, LCol, transformPriceInfo, transformOneTimePriceInfo} from "../Utils";
import {Graphic} from "../../Utils";

export const GraphicCell = (props) => {
    let icon = props.vas.thumbnailIcon ? props.vas.thumbnailIcon : "air-drop";
    return (
        <LCol big="3" medium="3" small="8" className="u-small-spacing-l">
            <Graphic icon={icon ? icon.toLowerCase().split('_').join("-") : ""} imgUrl={props.vas.imgUrl}/>
        </LCol>)
};

export const NameCell = (props) => {
    return(
        <LCol small="12" medium="4" big="4">
            <h4 className="u-font-standard u-margin" dangerouslySetInnerHTML={{__html:props.vas.title}}/>
            {props.vas.slogan && <p className="u-margin" dangerouslySetInnerHTML={{__html: props.vas.slogan}}></p>}
            {props.vas.detailsDescription && <p className="u-margin" aria-hidden="false" dangerouslySetInnerHTML={{__html: props.vas.detailsDescription}} />}
        </LCol>);
};

export const PriceCell = (props) => {
    return(
        <LCol small={props.small} medium={props.medium} big={props.big} className="u-large-text-right u-medium-text-right u-small-padding-l">
            <RwdPriceBox
                quantity = {props.quantity}
                prices={transformPriceInfo(props.vas.paymentDescriptions.monthlyPayments, props.net)}
                oneTimePrice={transformOneTimePriceInfo(props.vas.paymentDescriptions.oneTimePayment, props.net)}
                checkoutPrice={transformOneTimePriceInfo(props.vas.paymentDescriptions.payNowPayment, props.net)}
            />
        </LCol>
    );
};

PriceCell.defaultProps = {
    small: "12",
    medium: "3",
    big: "3",
    quantity: "1"
};

export const InputCell = (props) => {
    return(
        <LCol small="12" medium="2" big="2" className="u-small-position-absolute u-small-position-right u-small-w-auto u-small-padding-right-l">
            <label className="o-radio opl-radio">
                <input type="checkbox"
                    name="gadget-choose"
                    className="u-acc-hide"
                    checked={props.vas.selected}
                    disabled={props.vas.mandatory}
                    value={props.vas.id}
                    />
                <span className="o-ci"/>
                <span className="o-ci-label">
                    <span className="u-acc-txt--show">Wybieram</span>
                    <span className="u-acc-txt--hide">{props.vas.selected ? "Wybrano" : "Wybierz"}</span>
                </span>
            </label>
        </LCol>
    )
};

